/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/packages              ->  index
 * POST    /api/packages              ->  create
 * GET     /api/packages/:id          ->  show
 * PUT     /api/packages/:id          ->  upsert
 * PATCH   /api/packages/:id          ->  patch
 * DELETE  /api/packages/:id          ->  destroy
 */

'use strict';

import jsonpatch from 'fast-json-patch';
import Package from './package.model';
import Q from 'q';
import _ from 'lodash';
import rp from 'request-promise';
import moment from 'moment';
var hotelCtrl = require('../hotel/hotel.controller.js');
var flightCtrl = require('../flight/flight.controller.js');
var eventCtrl = require('../event/event.controller.js');

var travelportHotelCtrl = require('../travelportHotel/travelportHotel.controller.js');


function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function (entity) {
    if (entity) {
      return res.status(statusCode).json(entity);
    }
    return null;
  };
}

function patchUpdates(patches) {
  return function (entity) {
    try {
      jsonpatch.apply(entity, patches, /*validate*/ true);
    } catch (err) {
      return Promise.reject(err);
    }

    return entity.save();
  };
}

function removeEntity(res) {
  return function (entity) {
    if (entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function (entity) {
    if (!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function (err) {
    res.status(statusCode).send(err);
  };
}


function formatDate(date) {
  return moment(date).format('YYYY-MM-DD');
}

function lookUpAirportByLatLong(lat, long) {
  var airportLookupUrl = 'http://terminal2.expedia.com/x/geo/features?within=30km&lat=' + lat + '&lng=' + long + '&type=airport&verbose=3&apikey=BAGEROtURYYysKTHQIE7HK5m0tOFIjSH';
  return rp(airportLookupUrl);
}

function getAllData(id, lat, long, departureAirport, departureDate, eventId, arrivalDate, adults, childrens) {

  return lookUpAirportByLatLong(lat, long).then(dat => {
    let arrivalAirport = JSON.parse(dat)[0].tags.iata.airportCode.value;
    return Q.all([
      travelportHotelCtrl.getAllHotels(arrivalAirport,
        formatDate(departureDate),
        formatDate(arrivalDate),
        lat, long, adults, childrens),
      eventCtrl.getEventOffer(eventId)
    ]).then(function (data) {
      return Q.allSettled([
          _package(3, data, arrivalAirport,
            formatDate(departureDate),
            formatDate(arrivalDate),
            lat, long, adults, childrens),
          _package(2, data, arrivalAirport,
            formatDate(departureDate),
            formatDate(arrivalDate),
            lat, long, adults, childrens),
          _package(1, data, arrivalAirport,
            formatDate(departureDate),
            formatDate(arrivalDate),
            lat, long, adults, childrens),
          _package(0, data, arrivalAirport,
            formatDate(departureDate),
            formatDate(arrivalDate),
            lat, long, adults, childrens)
        ])
        .then(function (results) {
          return {
            gold: results[0],
            redCarpet: results[1],
            backstage: results[2],
            vip: results[3]
          };
        });
    });
  });
}



function _package(packageType, iter, arrivalAirport, departureDate, arrivalDate,
  lat, long, adults, children) {
  let defer = Q.defer();
  let hotels = _.orderBy(iter[0][0], ['HotelRating', 'distanceToEvent', 'lowRate'], ['desc', 'asc', 'asc']);
  let chunkedHotels = _.chunk(hotels, hotels.length / 4)[packageType];
  // var flight = _.orderBy(iter[1], function (a) {
  //   return Number(a.totalFare);
  // });
  var events = _.cloneDeep(iter[1]);
  events.offers = _.each(events.offers, function (ticketTypes) {
    ticketTypes.attributes.prices = _.orderBy(ticketTypes.attributes.prices, function (a) {
      return Number(a.priceZone);
    });
    if (ticketTypes.attributes.prices.length > 3) {
      ticketTypes.attributes.prices = _.chunk(ticketTypes.attributes.prices, ticketTypes.attributes.prices.length / 4)[packageType];
    }
  });
  return travelportHotelCtrl.getHotelRates(chunkedHotels, arrivalAirport,
      formatDate(departureDate),
      formatDate(arrivalDate),
      lat, long, adults, children)
    .then(dat => {
      let mergedHotels = _.map(dat, function (item, i) {
        return _.merge(item, chunkedHotels[i]);
      });
      defer.resolve({
        hotel: mergedHotels,
        // flight: _.chunk(flight, flight.length / 4)[0],
        events
      });
      return defer.promise;
    })
    .catch(err => {
      console.warn(err, 'err occured ************************')
    });
}
// Gets a list of Packages
export function index(req, res) {
  return Package.find().exec()
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Gets a single Package from the DB
// ids are for packages type
// 0 = gold & 3 = vip
export function show(req, res) {
  getAllData(req.params.id,
      req.query.lat,
      req.query.long,
      req.query.departureAirport,
      req.query.departureDate,
      req.query.eventId,
      req.query.arrivalDate,
      Number(req.query.adults),
      Number(req.query.childrens))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Creates a new Package in the DB
export function create(req, res) {
  return Package.create(req.body)
    .then(respondWithResult(res, 201))
    .catch(handleError(res));
}

// Upserts the given Package in the DB at the specified ID
export function upsert(req, res) {
  if (req.body._id) {
    delete req.body._id;
  }
  return Package.findOneAndUpdate({
    _id: req.params.id
  }, req.body, {
    upsert: true,
    setDefaultsOnInsert: true,
    runValidators: true
  }).exec()

  .then(respondWithResult(res))
    .catch(handleError(res));
}

// Updates an existing Package in the DB
export function patch(req, res) {
  if (req.body._id) {
    delete req.body._id;
  }
  return Package.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(patchUpdates(req.body))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Deletes a Package from the DB
export function destroy(req, res) {
  return Package.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(removeEntity(res))
    .catch(handleError(res));
}
